
import { defineComponent, computed } from 'vue'

export default defineComponent({
  name: 'movie-button',
  props: {
    isSearch: {
      type: Boolean,
      default: false
    },
    isActive: {
      type: Boolean,
      default: false
    }
  },
  setup(props) {
    const buttonClasses = computed(() => {
      return {
        'button--active': props.isActive || props.isSearch,
        'button--search': props.isSearch,
      }
    })
    
    return {
      buttonClasses,
    }
  }
})
